@use "../../../shared/styles/mixins" as *;

.Label {
  @include visually-hidden-focusable;
}

.Icon {
  display: block;
  margin: auto;

  @include color-transition((color: var(--primary-text-color)));
}

.FavouriteButton {
  aspect-ratio: 1;
  border: 1px solid;
  padding: 0;
  cursor: pointer;

  @include color-transition((
    background-color: var(--icon-button-color),
    border-color: var(--icon-button-border-color)
  ));

  &_active {
    background-color: transparent;
    border-color: var(--icon-button-border-state-color);
  }

  &:hover {
    background-color: var(--icon-button-hover-color);
    border-color: var(--icon-button-border-hover-color);
  }
}
